.eggContainer {

    text-align: center;

    a {
        text-decoration: none;
    }

    .eggWrapper {
        position: relative;
        display: inline-flex;
        margin-left: 1em;
        margin-top: 5vh;
        width: 10em;
        height: 12em;

        .eggDiv,
        .eggTop {
            position: absolute;
            width: 10em;
            height: 10em;
            border-radius: 80% 20% 55% 50% / 55% 20% 80% 50%;
            transform: rotate(-45deg);
            background-color: #ffffee;
            box-shadow: inset 1em 1em 2em rgba(0, 0, 0, 0.3),
                inset 0.2em 0.2em 1em rgba(0, 0, 0, 0.3);
            clip-path: polygon(-35.87% 83.08%,
                    244% 155.05%,
                    108.11% 49.6%,
                    76.11% 57.69%,
                    71.33% 37.01%,
                    44.85% 22.33%,
                    32.41% -13.39%);
            margin-top: 2em;
            transition: transform 300ms cubic-bezier(0.5, -0.5, 0.5, 1.5) 0s;
        }


        .eggTop {
            transform: rotate(-45deg) translateY(0.1em) translateX(-0.1em) scale(1.016);
            clip-path: polygon(27.84% -22.62%,
                    123.57% -5.52%,
                    108.11% 49.6%,
                    76.11% 57.69%,
                    71.33% 37.01%,
                    44.85% 22.33%,
                    32.41% -13.39%);

            transform: translateY(-3em) rotate(-45deg);
        }

        .chickHead {
            position: absolute;
            top: 1em;
            width: 7em;
            height: 7em;
            margin-left: 1.5em;
            margin-top: 2em;
            background-color: #ffff55;
            box-shadow: inset 1em 1em 2em rgba(0, 0, 0, 0.3),
                inset 0 2em 1em rgba(0, 0, 0, 0.3);
            border-radius: 100%;
            transition: transform 300ms ease-in-out 0s;
            transform: translateY(-3em);

            .eyeDiv {
                position: relative;
                width: 1em;
                height: 1em;
                float: left;
                border-radius: 100%;
                margin: 0.3em;
                margin-top: 3em;
                background-color: #666666;
                box-shadow: inset 0.3em -0.3em 0.5em rgba(0, 0, 0, 0.8);
                animation-name: blinkAnim;
                animation-duration: 3s;
                animation-timing-function: ease-in-out;
                animation-iteration-count: infinite;

                &:nth-of-type(2) {
                    float: right;
                    animation-delay: 20ms;
                }
            }

            .beakDiv {
                position: absolute;
                width: 1em;
                height: 1em;
                margin-top: 3.2em;
                margin-left: 3em;
                border-radius: 80% 20% 55% 50% / 55% 20% 80% 50%;
                transform: rotate(135deg);
                background-color: #ff5800;
                border: 0.1em solid #ffff55;
                box-shadow: inset -0.3em 0.3em 0.5em rgba(0, 0, 0, 0.3);
            }
        }

        // &:hover {
        //     .eggTop {
        //         /*margin-top: -1vw;*/
        //         transform: translateY(-3em) rotate(-45deg);
        //     }

        //     .chickHead {
        //         transform: translateY(-3em);
        //     }
        // }
    }

    .eggTitle {
        font-size: 3em;
        font-family: 'Cookie', cursive;
        color: white;
    }
}

@keyframes blinkAnim {
    0% {
        transform: scaleY(1);
    }

    5% {
        transform: scaleY(0.1);
    }

    10% {
        transform: scaleY(0.1);
    }

    15% {
        transform: scaleY(1);
    }

    100% {
        transform: scaleY(1);
    }
}